.keyboard-rows{
    display: flex;
    flex-direction: column;
    gap: 8px;
    align-items: center;
    margin-bottom: 10px;
}
.row{
    width: auto;
    display: flex;
    flex-direction: row;
    gap: 5px;
}

button{
    border-radius: 5px;
    font-weight: bold;
    cursor: pointer;
    background: #d3d6da;
    padding: 22px 18px;
    border: none;
    text-transform: uppercase;
}
.key-correct{
    background: #6aaa64;
    color: #fff;
}

.key-present{
    background: #c9b458;
    color: #fff;
}

.key-absent{
    background: #787c7e;
    color: #fff;
}


@media screen and (max-width: 600px) {
    .keyboard-rows{
        display: flex;
        flex-direction: column;
        gap: 5px;
        align-items: center;
        margin-bottom: 5px;
    }
    .row{
        width: auto;
        display: flex;
        flex-direction: row;
        gap: 3px;
    }
    button{
        border-radius: 5px;
        font-weight: bold;
        cursor: pointer;
        background: #d3d6da;
        padding: 11px 9px;
        border: none;
        text-transform: uppercase;
    }
    
}